"use client"

import React, { useState, useEffect, useRef, useCallback } from "react"
import Link from "next/link"
import { usePathname, useRouter } from "next/navigation"
import { Suspense } from "react"
import { useAuth } from "@/lib/auth-context"
import {
  Wallet,
  LayoutDashboard,
  ArrowDownToLine,
  ArrowLeftRight,
  PiggyBank,
  Target,
  Settings,
  Percent,
  Code,
  FileText,
  LogOut,
  Menu,
  X,
  ChevronRight,
  Bell,
  Search,
  Loader2,
  CheckCircle2,
  AlertTriangle,
  Shield,
  ArrowUpRight,
  ArrowDownRight,
  Clock,
  User,
  Zap,
  Command,
} from "lucide-react"

type MenuSection = "financeiro" | "perfil" | "desenvolvimento" | null

interface Notification {
  id: string
  type: "deposit" | "transfer" | "security"
  title: string
  description: string
  amount?: string
  time: string
  read: boolean
}

interface SearchResult {
  id: string
  type: "transaction" | "user" | "action"
  title: string
  description: string
  icon: React.ElementType
  href?: string
  action?: () => void
}

const menuItems = {
  inicio: {
    icon: LayoutDashboard,
    label: "Dashboard",
    href: "/dashboard",
  },
  carteira: {
    icon: Wallet,
    label: "Carteira",
    href: "/dashboard/carteira",
  },
  financeiro: {
    icon: ArrowLeftRight,
    label: "Financeiro",
    items: [
      { icon: ArrowLeftRight, label: "Transferir", href: "/dashboard/transferencias" },
      { icon: ArrowDownToLine, label: "Depositar", href: "/dashboard/depositos" },
      { icon: PiggyBank, label: "Caixinhas", href: "/dashboard/caixinhas" },
      { icon: Target, label: "Metas", href: "/dashboard/metas" },
    ],
  },
  perfil: {
    icon: Settings,
    label: "Perfil",
    items: [
      { icon: Settings, label: "Configuracoes", href: "/dashboard/configuracoes" },
      { icon: Percent, label: "Taxas", href: "/dashboard/taxas" },
    ],
  },
  desenvolvimento: {
    icon: Code,
    label: "Desenvolvimento",
    items: [
      { icon: Code, label: "Credenciais", href: "/dashboard/credenciais" },
      { icon: FileText, label: "Ver documentacao", href: "https://docs.fluxpay.com", external: true },
    ],
  },
}


export default function DashboardLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const pathname = usePathname()
  const router = useRouter()
  const { user, isLoading, isAuthenticated, logout } = useAuth()
  const [sidebarOpen, setSidebarOpen] = useState(false)
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false)
  const [expandedMenu, setExpandedMenu] = useState<MenuSection>(null)
  
  // Notifications
  const [notificationsOpen, setNotificationsOpen] = useState(false)
  const [notifications, setNotifications] = useState<Notification[]>([])
  const notificationsRef = useRef<HTMLDivElement>(null)

  // Fetch notifications from API
  useEffect(() => {
    const fetchNotifications = async () => {
      if (!isAuthenticated || !user) return

      try {
        const accessToken = localStorage.getItem("frcabuloso_access_token")
        if (!accessToken) return

        const response = await fetch("/api/notifications", {
          headers: {
            "Authorization": `Bearer ${accessToken}`,
          },
        })

        if (response.ok) {
          const data = await response.json()
          setNotifications(data.notifications || [])
        }
      } catch (error) {
        console.error("Error fetching notifications:", error)
      }
    }

    fetchNotifications()
  }, [isAuthenticated, user])
  
  // Search
  const [searchOpen, setSearchOpen] = useState(false)
  const [searchQuery, setSearchQuery] = useState("")
  const [searchResults, setSearchResults] = useState<SearchResult[]>([])
  const [selectedSearchIndex, setSelectedSearchIndex] = useState(0)
  const searchRef = useRef<HTMLDivElement>(null)
  const searchInputRef = useRef<HTMLInputElement>(null)

  // Redirect to login if not authenticated
  useEffect(() => {
    if (!isLoading && !isAuthenticated) {
      router.push("/login")
    }
  }, [isLoading, isAuthenticated, router])

  // Keyboard shortcut for search (CTRL+K)
  useEffect(() => {
    const handleKeyDown = (e: KeyboardEvent) => {
      if ((e.metaKey || e.ctrlKey) && e.key === "k") {
        e.preventDefault()
        setSearchOpen(true)
        setTimeout(() => searchInputRef.current?.focus(), 100)
      }
      if (e.key === "Escape") {
        setSearchOpen(false)
        setNotificationsOpen(false)
      }
    }
    
    window.addEventListener("keydown", handleKeyDown)
    return () => window.removeEventListener("keydown", handleKeyDown)
  }, [])

  // Close dropdowns on click outside
  useEffect(() => {
    const handleClickOutside = (e: MouseEvent) => {
      if (notificationsRef.current && !notificationsRef.current.contains(e.target as Node)) {
        setNotificationsOpen(false)
      }
      if (searchRef.current && !searchRef.current.contains(e.target as Node)) {
        setSearchOpen(false)
      }
    }
    
    document.addEventListener("mousedown", handleClickOutside)
    return () => document.removeEventListener("mousedown", handleClickOutside)
  }, [])

  // Search logic
  const performSearch = useCallback((query: string) => {
    if (!query.trim()) {
      setSearchResults([])
      return
    }
    
    const q = query.toLowerCase()
    const results: SearchResult[] = []
    
    // Transactions
    const transactionResults = [
      { id: "t1", title: "Deposito #DEP001", description: "R$ 500,00 - Pix", date: "20 Jan" },
      { id: "t2", title: "Transferencia #TRF001", description: "R$ 150,00 - Maria Santos", date: "19 Jan" },
      { id: "t3", title: "Deposito #DEP002", description: "R$ 1.000,00 - Pix", date: "18 Jan" },
    ].filter(t => t.title.toLowerCase().includes(q) || t.description.toLowerCase().includes(q))
    
    transactionResults.forEach(t => {
      results.push({
        id: t.id,
        type: "transaction",
        title: t.title,
        description: `${t.description} - ${t.date}`,
        icon: t.title.includes("Deposito") ? ArrowDownRight : ArrowUpRight,
        href: "/dashboard/carteira",
      })
    })
    
    // Users
    const userResults = [
      { id: "u1", name: "Maria Santos", email: "maria@email.com" },
      { id: "u2", name: "Pedro Silva", email: "pedro@email.com" },
      { id: "u3", name: "Ana Costa", email: "ana@email.com" },
    ].filter(u => u.name.toLowerCase().includes(q) || u.email.toLowerCase().includes(q))
    
    userResults.forEach(u => {
      results.push({
        id: u.id,
        type: "user",
        title: u.name,
        description: u.email,
        icon: User,
        href: "/dashboard/transferencias",
      })
    })
    
    // Quick actions
    const actions = [
      { id: "a1", title: "Fazer deposito", description: "Adicione saldo a sua carteira", icon: ArrowDownToLine, href: "/dashboard/depositos" },
      { id: "a2", title: "Nova transferencia", description: "Envie dinheiro para alguem", icon: ArrowLeftRight, href: "/dashboard/transferencias" },
      { id: "a3", title: "Ver extrato", description: "Consulte seu historico de transacoes", icon: FileText, href: "/dashboard/carteira" },
      { id: "a4", title: "Configuracoes", description: "Gerencie sua conta", icon: Settings, href: "/dashboard/configuracoes" },
    ].filter(a => a.title.toLowerCase().includes(q) || a.description.toLowerCase().includes(q))
    
    actions.forEach(a => {
      results.push({
        id: a.id,
        type: "action",
        title: a.title,
        description: a.description,
        icon: a.icon,
        href: a.href,
      })
    })
    
    setSearchResults(results)
    setSelectedSearchIndex(0)
  }, [])

  useEffect(() => {
    performSearch(searchQuery)
  }, [searchQuery, performSearch])

  // Search keyboard navigation
  const handleSearchKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === "ArrowDown") {
      e.preventDefault()
      setSelectedSearchIndex(prev => Math.min(prev + 1, searchResults.length - 1))
    } else if (e.key === "ArrowUp") {
      e.preventDefault()
      setSelectedSearchIndex(prev => Math.max(prev - 1, 0))
    } else if (e.key === "Enter" && searchResults[selectedSearchIndex]) {
      e.preventDefault()
      const result = searchResults[selectedSearchIndex]
      if (result.href) {
        router.push(result.href)
        setSearchOpen(false)
        setSearchQuery("")
      }
    }
  }

  const markAllNotificationsRead = () => {
    setNotifications([])
  }

  const unreadCount = notifications.filter(n => !n.read).length

  const toggleMenu = (menu: MenuSection) => {
    setExpandedMenu(expandedMenu === menu ? null : menu)
  }

  const isItemActive = (href: string) => pathname === href

  const isMenuActive = (items: { href: string }[]) => {
    return items.some((item) => pathname === item.href)
  }

  const getInitials = (name: string) => {
    return name
      .split(" ")
      .map((n) => n[0])
      .join("")
      .toUpperCase()
      .slice(0, 2)
  }

  const handleLogout = () => {
    logout()
  }

  const renderMenuItem = (
    icon: React.ElementType,
    label: string,
    href: string,
    external?: boolean
  ) => {
    const Icon = icon
    const isActive = isItemActive(href)

    if (external) {
      return (
        <a
          href={href}
          target="_blank"
          rel="noopener noreferrer"
          className={`flex items-center gap-3 px-3 py-2.5 rounded-lg transition-all duration-200 ${
            isActive
              ? "bg-emerald-500/10 text-emerald-400"
              : "text-zinc-400 hover:text-white hover:bg-zinc-800/50"
          }`}
        >
          <Icon className="w-5 h-5 flex-shrink-0" />
          {!sidebarCollapsed && <span className="text-sm font-medium">{label}</span>}
        </a>
      )
    }

    return (
      <Link
        href={href}
        onClick={() => setSidebarOpen(false)}
        className={`flex items-center gap-3 px-3 py-2.5 rounded-lg transition-all duration-200 ${
          isActive
            ? "bg-emerald-500/10 text-emerald-400"
            : "text-zinc-400 hover:text-white hover:bg-zinc-800/50"
        }`}
      >
        <Icon className="w-5 h-5 flex-shrink-0" />
        {!sidebarCollapsed && <span className="text-sm font-medium">{label}</span>}
      </Link>
    )
  }

  const renderExpandableMenu = (
    key: MenuSection,
    icon: React.ElementType,
    label: string,
    items: { icon: React.ElementType; label: string; href: string; external?: boolean }[]
  ) => {
    const Icon = icon
    const isExpanded = expandedMenu === key
    const hasActiveItem = isMenuActive(items)

    return (
      <div>
        <button
          type="button"
          onClick={() => toggleMenu(key)}
          className={`w-full flex items-center justify-between px-3 py-2.5 rounded-lg transition-all duration-200 ${
            hasActiveItem
              ? "bg-emerald-500/5 text-emerald-400"
              : "text-zinc-400 hover:text-white hover:bg-zinc-800/50"
          }`}
        >
          <div className="flex items-center gap-3">
            <Icon className="w-5 h-5 flex-shrink-0" />
            {!sidebarCollapsed && <span className="text-sm font-medium">{label}</span>}
          </div>
          {!sidebarCollapsed && (
            <ChevronRight
              className={`w-4 h-4 transition-transform duration-200 ${isExpanded ? "rotate-90" : ""}`}
            />
          )}
        </button>
        {!sidebarCollapsed && (
          <div
            className={`overflow-hidden transition-all duration-300 ease-in-out ${
              isExpanded ? "max-h-64 opacity-100" : "max-h-0 opacity-0"
            }`}
          >
            <div className="pl-4 mt-1 space-y-1 border-l border-zinc-800 ml-5">
              {items.map((item) => (
                <div key={item.href}>
                  {item.external ? (
                    <a
                      href={item.href}
                      target="_blank"
                      rel="noopener noreferrer"
                      className={`flex items-center gap-3 px-3 py-2 rounded-lg transition-all duration-200 text-zinc-400 hover:text-white hover:bg-zinc-800/50`}
                    >
                      <item.icon className="w-4 h-4 flex-shrink-0" />
                      <span className="text-sm">{item.label}</span>
                    </a>
                  ) : (
                    <Link
                      href={item.href}
                      onClick={() => setSidebarOpen(false)}
                      className={`flex items-center gap-3 px-3 py-2 rounded-lg transition-all duration-200 ${
                        isItemActive(item.href)
                          ? "bg-emerald-500/10 text-emerald-400"
                          : "text-zinc-400 hover:text-white hover:bg-zinc-800/50"
                      }`}
                    >
                      <item.icon className="w-4 h-4 flex-shrink-0" />
                      <span className="text-sm">{item.label}</span>
                    </Link>
                  )}
                </div>
              ))}
            </div>
          </div>
        )}
      </div>
    )
  }

  const getNotificationIcon = (type: Notification["type"]) => {
    switch (type) {
      case "deposit":
        return <ArrowDownRight className="w-4 h-4 text-emerald-400" />
      case "transfer":
        return <ArrowUpRight className="w-4 h-4 text-blue-400" />
      case "security":
        return <Shield className="w-4 h-4 text-amber-400" />
    }
  }

  // Show loading while checking auth
  if (isLoading) {
    return (
      <div className="min-h-screen bg-[#09090B] flex items-center justify-center">
        <div className="flex flex-col items-center gap-4">
          <Loader2 className="w-8 h-8 text-emerald-500 animate-spin" />
          <p className="text-zinc-400 text-sm">Carregando...</p>
        </div>
      </div>
    )
  }

  // Don't render if not authenticated
  if (!isAuthenticated || !user) {
    return null
  }

  return (
    <div className="min-h-screen bg-[#09090B]">
      {sidebarOpen && (
        <div
          className="fixed inset-0 bg-black/60 z-40 lg:hidden"
          onClick={() => setSidebarOpen(false)}
        />
      )}

      <aside
        className={`fixed top-0 left-0 z-50 h-full bg-zinc-900/95 border-r border-zinc-800 transform transition-all duration-300 lg:translate-x-0 ${
          sidebarOpen ? "translate-x-0" : "-translate-x-full"
        } ${sidebarCollapsed ? "w-20" : "w-64"}`}
      >
        <div className="flex flex-col h-full">
          <div className="flex items-center justify-between p-4 border-b border-zinc-800">
            <Link href="/dashboard" className="flex items-center gap-2">
              <div className="w-8 h-8 rounded-lg bg-emerald-500 flex items-center justify-center flex-shrink-0">
                <Wallet className="w-4 h-4 text-white" />
              </div>
              {!sidebarCollapsed && (
                <span className="text-white font-semibold text-lg">fluxpay</span>
              )}
            </Link>
            <button
              type="button"
              onClick={() => setSidebarOpen(false)}
              className="lg:hidden text-zinc-400 hover:text-white"
            >
              <X className="w-5 h-5" />
            </button>
            <button
              type="button"
              onClick={() => setSidebarCollapsed(!sidebarCollapsed)}
              className="hidden lg:block text-zinc-400 hover:text-white"
            >
              <Menu className="w-5 h-5" />
            </button>
          </div>

          <nav className="flex-1 p-4 space-y-2 overflow-y-auto">
            <div className="space-y-1">
              {!sidebarCollapsed && (
                <p className="px-3 text-xs font-semibold text-zinc-500 uppercase tracking-wider mb-2">
                  Inicio
                </p>
              )}
              {renderMenuItem(menuItems.inicio.icon, menuItems.inicio.label, menuItems.inicio.href)}
            </div>

            <div className="space-y-1 pt-4">
              {!sidebarCollapsed && (
                <p className="px-3 text-xs font-semibold text-zinc-500 uppercase tracking-wider mb-2">
                  Carteira
                </p>
              )}
              {renderMenuItem(menuItems.carteira.icon, menuItems.carteira.label, menuItems.carteira.href)}
            </div>

            <div className="space-y-1 pt-4">
              {!sidebarCollapsed && (
                <p className="px-3 text-xs font-semibold text-zinc-500 uppercase tracking-wider mb-2">
                  Financeiro
                </p>
              )}
              {renderExpandableMenu(
                "financeiro",
                menuItems.financeiro.icon,
                menuItems.financeiro.label,
                menuItems.financeiro.items
              )}
            </div>

            <div className="space-y-1 pt-4">
              {!sidebarCollapsed && (
                <p className="px-3 text-xs font-semibold text-zinc-500 uppercase tracking-wider mb-2">
                  Perfil
                </p>
              )}
              {renderExpandableMenu(
                "perfil",
                menuItems.perfil.icon,
                menuItems.perfil.label,
                menuItems.perfil.items
              )}
            </div>

            <div className="space-y-1 pt-4">
              {!sidebarCollapsed && (
                <p className="px-3 text-xs font-semibold text-zinc-500 uppercase tracking-wider mb-2">
                  Desenvolvimento
                </p>
              )}
              {renderExpandableMenu(
                "desenvolvimento",
                menuItems.desenvolvimento.icon,
                menuItems.desenvolvimento.label,
                menuItems.desenvolvimento.items
              )}
            </div>
          </nav>

          <div className="p-4 border-t border-zinc-800">
            <div className={`flex items-center gap-3 mb-4 ${sidebarCollapsed ? "justify-center" : ""}`}>
              {user.picture ? (
                <img
                  src={user.picture || "/placeholder.svg"}
                  alt={user.name}
                  className="w-10 h-10 rounded-full border-2 border-emerald-500/30 flex-shrink-0"
                />
              ) : (
                <div className="w-10 h-10 rounded-full bg-emerald-500/20 flex items-center justify-center flex-shrink-0">
                  <span className="text-emerald-400 font-bold text-sm">{getInitials(user.name)}</span>
                </div>
              )}
              {!sidebarCollapsed && (
                <div className="flex-1 min-w-0">
                  <p className="text-white text-sm font-medium truncate">{user.name}</p>
                  <p className="text-zinc-500 text-xs truncate">{user.email}</p>
                </div>
              )}
            </div>
            <button
              type="button"
              onClick={handleLogout}
              className={`flex items-center gap-2 px-3 py-2 text-zinc-400 hover:text-red-400 rounded-lg hover:bg-red-500/10 transition-colors w-full ${
                sidebarCollapsed ? "justify-center" : ""
              }`}
            >
              <LogOut className="w-4 h-4" />
              {!sidebarCollapsed && <span className="text-sm">Sair</span>}
            </button>
          </div>
        </div>
      </aside>

      <div className={`transition-all duration-300 ${sidebarCollapsed ? "lg:pl-20" : "lg:pl-64"}`}>
        <header className="sticky top-0 z-30 bg-[#09090B]/80 backdrop-blur-xl border-b border-zinc-800">
          <div className="flex items-center justify-between px-4 lg:px-6 py-4">
            <div className="flex items-center gap-4">
              <button
                type="button"
                onClick={() => setSidebarOpen(true)}
                className="lg:hidden text-zinc-400 hover:text-white"
              >
                <Menu className="w-6 h-6" />
              </button>

              {/* Search */}
              <div className="relative" ref={searchRef}>
                <button
                  type="button"
                  onClick={() => {
                    setSearchOpen(true)
                    setTimeout(() => searchInputRef.current?.focus(), 100)
                  }}
                  className="hidden sm:flex items-center gap-2 px-3 py-2 bg-zinc-900/50 border border-zinc-800 rounded-lg hover:border-zinc-700 transition-colors"
                >
                  <Search className="w-4 h-4 text-zinc-500" />
                  <span className="text-sm text-zinc-500 w-48 lg:w-64 text-left">Buscar...</span>
                  <kbd className="hidden lg:inline-flex items-center px-2 py-0.5 text-xs text-zinc-500 bg-zinc-800 rounded gap-0.5">
                    <Command className="w-3 h-3" /> K
                  </kbd>
                </button>

                {/* Search Dropdown */}
                {searchOpen && (
                  <div className="fixed sm:absolute inset-x-4 sm:inset-x-auto sm:left-0 top-16 sm:top-0 sm:w-96 bg-zinc-900 border border-zinc-800 rounded-xl shadow-2xl shadow-black/50 overflow-hidden z-50">
                    <div className="flex items-center gap-2 px-4 py-3 border-b border-zinc-800">
                      <Search className="w-4 h-4 text-zinc-500" />
                      <input
                        ref={searchInputRef}
                        type="text"
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        onKeyDown={handleSearchKeyDown}
                        placeholder="Buscar transacoes, usuarios, acoes..."
                        className="flex-1 bg-transparent text-white text-sm placeholder:text-zinc-500 focus:outline-none"
                        autoFocus
                      />
                      <button
                        type="button"
                        onClick={() => {
                          setSearchOpen(false)
                          setSearchQuery("")
                        }}
                        className="text-zinc-500 hover:text-white"
                      >
                        <X className="w-4 h-4" />
                      </button>
                    </div>

                    <div className="max-h-96 overflow-y-auto">
                      {searchResults.length > 0 ? (
                        <div className="p-2">
                          {["transaction", "user", "action"].map((type) => {
                            const typeResults = searchResults.filter((r) => r.type === type)
                            if (typeResults.length === 0) return null
                            return (
                              <div key={type} className="mb-2">
                                <p className="px-2 py-1 text-xs font-semibold text-zinc-500 uppercase">
                                  {type === "transaction" ? "Transacoes" : type === "user" ? "Usuarios" : "Acoes rapidas"}
                                </p>
                                {typeResults.map((result, index) => {
                                  const globalIndex = searchResults.indexOf(result)
                                  return (
                                    <button
                                      key={result.id}
                                      type="button"
                                      onClick={() => {
                                        if (result.href) {
                                          router.push(result.href)
                                          setSearchOpen(false)
                                          setSearchQuery("")
                                        }
                                      }}
                                      className={`w-full flex items-center gap-3 px-3 py-2 rounded-lg transition-colors ${
                                        globalIndex === selectedSearchIndex
                                          ? "bg-emerald-500/10 text-emerald-400"
                                          : "text-zinc-400 hover:bg-zinc-800/50"
                                      }`}
                                    >
                                      <result.icon className="w-4 h-4 flex-shrink-0" />
                                      <div className="flex-1 text-left">
                                        <p className={`text-sm font-medium ${globalIndex === selectedSearchIndex ? "text-white" : "text-zinc-300"}`}>
                                          {result.title}
                                        </p>
                                        <p className="text-xs text-zinc-500">{result.description}</p>
                                      </div>
                                      <ChevronRight className="w-4 h-4 text-zinc-600" />
                                    </button>
                                  )
                                })}
                              </div>
                            )
                          })}
                        </div>
                      ) : searchQuery ? (
                        <div className="p-8 text-center">
                          <Search className="w-8 h-8 text-zinc-600 mx-auto mb-3" />
                          <p className="text-zinc-500 text-sm">Nenhum resultado encontrado</p>
                        </div>
                      ) : (
                        <div className="p-4">
                          <p className="px-2 py-1 text-xs font-semibold text-zinc-500 uppercase mb-2">Acoes rapidas</p>
                          {[
                            { icon: ArrowDownToLine, label: "Fazer deposito", href: "/dashboard/depositos" },
                            { icon: ArrowLeftRight, label: "Nova transferencia", href: "/dashboard/transferencias" },
                            { icon: Wallet, label: "Ver carteira", href: "/dashboard/carteira" },
                          ].map((action) => (
                            <button
                              key={action.label}
                              type="button"
                              onClick={() => {
                                router.push(action.href)
                                setSearchOpen(false)
                              }}
                              className="w-full flex items-center gap-3 px-3 py-2 text-zinc-400 hover:bg-zinc-800/50 rounded-lg transition-colors"
                            >
                              <action.icon className="w-4 h-4" />
                              <span className="text-sm">{action.label}</span>
                            </button>
                          ))}
                        </div>
                      )}
                    </div>

                    <div className="px-4 py-2 border-t border-zinc-800 flex items-center justify-between text-xs text-zinc-500">
                      <span>Navegar com ↑↓</span>
                      <span>Selecionar com Enter</span>
                    </div>
                  </div>
                )}
              </div>
            </div>

            <div className="flex items-center gap-3">
              {/* Notifications */}
              <div className="relative" ref={notificationsRef}>
                <button
                  type="button"
                  onClick={() => setNotificationsOpen(!notificationsOpen)}
                  className="relative p-2 text-zinc-400 hover:text-white rounded-lg hover:bg-zinc-800/50 transition-colors"
                >
                  <Bell className="w-5 h-5" />
                  {unreadCount > 0 && (
                    <span className="absolute top-1 right-1 w-4 h-4 bg-emerald-500 rounded-full text-[10px] font-bold text-white flex items-center justify-center">
                      {unreadCount}
                    </span>
                  )}
                </button>

                {/* Notifications Dropdown */}
                {notificationsOpen && (
                  <div className="fixed sm:absolute inset-x-4 sm:inset-x-auto sm:right-0 top-16 sm:top-full sm:mt-2 sm:w-96 bg-zinc-900 border border-zinc-800 rounded-xl shadow-2xl shadow-black/50 overflow-hidden animate-in fade-in slide-in-from-top-2 duration-200 z-50">
                    <div className="flex items-center justify-between px-4 py-3 border-b border-zinc-800">
                      <h3 className="text-white font-medium">Notificacoes</h3>
                      {unreadCount > 0 && (
                        <button
                          type="button"
                          onClick={markAllNotificationsRead}
                          className="text-emerald-400 text-xs hover:underline"
                        >
                          Marcar todas como lidas
                        </button>
                      )}
                    </div>

                    <div className="max-h-96 overflow-y-auto">
                      {notifications.length > 0 ? (
                        <div className="divide-y divide-zinc-800/50">
                          {notifications.map((notification) => (
                            <div
                              key={notification.id}
                              className={`px-4 py-3 hover:bg-zinc-800/30 transition-colors cursor-pointer ${
                                !notification.read ? "bg-emerald-500/5" : ""
                              }`}
                              onClick={() => {
                                setNotifications((prev) =>
                                  prev.map((n) =>
                                    n.id === notification.id ? { ...n, read: true } : n
                                  )
                                )
                              }}
                            >
                              <div className="flex items-start gap-3">
                                <div className={`w-8 h-8 rounded-lg flex items-center justify-center flex-shrink-0 ${
                                  notification.type === "deposit" ? "bg-emerald-500/10" :
                                  notification.type === "transfer" ? "bg-blue-500/10" : "bg-amber-500/10"
                                }`}>
                                  {getNotificationIcon(notification.type)}
                                </div>
                                <div className="flex-1 min-w-0">
                                  <div className="flex items-center gap-2">
                                    <p className="text-white text-sm font-medium truncate">{notification.title}</p>
                                    {!notification.read && (
                                      <span className="w-2 h-2 bg-emerald-500 rounded-full flex-shrink-0" />
                                    )}
                                  </div>
                                  <p className="text-zinc-500 text-xs mt-0.5">{notification.description}</p>
                                  <div className="flex items-center gap-2 mt-1">
                                    {notification.amount && (
                                      <span className={`text-xs font-medium ${
                                        notification.type === "deposit" || (notification.type === "transfer" && notification.title.includes("recebida"))
                                          ? "text-emerald-400"
                                          : "text-zinc-400"
                                      }`}>
                                        {notification.type === "deposit" || (notification.type === "transfer" && notification.title.includes("recebida")) ? "+" : "-"}{notification.amount}
                                      </span>
                                    )}
                                    <span className="text-zinc-600 text-xs">{notification.time}</span>
                                  </div>
                                </div>
                              </div>
                            </div>
                          ))}
                        </div>
                      ) : (
                        <div className="p-8 text-center">
                          <Bell className="w-8 h-8 text-zinc-600 mx-auto mb-3" />
                          <p className="text-zinc-500 text-sm">Nenhuma notificacao</p>
                        </div>
                      )}
                    </div>

                    <div className="px-4 py-3 border-t border-zinc-800">
                      <Link
                        href="/dashboard/configuracoes"
                        className="text-emerald-400 text-sm hover:underline"
                        onClick={() => setNotificationsOpen(false)}
                      >
                        Gerenciar notificacoes
                      </Link>
                    </div>
                  </div>
                )}
              </div>

              <Link
                href="/dashboard/depositos"
                className="hidden sm:flex items-center gap-2 px-4 py-2 bg-emerald-500 hover:bg-emerald-400 text-zinc-900 text-sm font-medium rounded-lg transition-colors"
              >
                <ArrowDownToLine className="w-4 h-4" />
                Depositar
              </Link>
            </div>
          </div>
        </header>

        <main className="p-4 lg:p-6">
          <Suspense fallback={<div className="flex items-center justify-center min-h-[400px]"><div className="w-8 h-8 border-2 border-emerald-500 border-t-transparent rounded-full animate-spin" /></div>}>{children}</Suspense>
        </main>
      </div>
    </div>
  )
}
